import { readFile } from "node:fs/promises";
import { octokit } from "./octokit.js";
export async function getPRDetails() {
    const { repository, number } = JSON.parse(await readFile(process.env.GITHUB_EVENT_PATH || "", "utf8"));
    const prResponse = await octokit.pulls.get({
        owner: repository.owner.login,
        repo: repository.name,
        pull_number: number,
    });
    return {
        owner: repository.owner.login,
        repo: repository.name,
        pull_number: number,
        title: prResponse.data.title ?? "",
        description: prResponse.data.body ?? "",
    };
}
export async function createReviewComment(owner, repo, pull_number, comments) {
    const event = comments.some(({ severity }) => severity === "critical") ? "REQUEST_CHANGES" : "COMMENT";
    return octokit.pulls.createReview({
        owner,
        repo,
        pull_number,
        comments: comments.map(({ file, lineNumber, reviewComment }) => ({
            body: reviewComment,
            path: file,
            line: lineNumber,
        })),
        event,
    });
}
export const updatePRDescription = async (owner, repo, pull_number, description) => {
    return octokit.pulls.update({
        owner,
        repo,
        pull_number,
        body: description,
    });
};
